import React, { useEffect, useState } from "react";
import Typography from '@material-ui/core/Typography';
import Layout from '../layout/Layout';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import ShowBlog from '../components/ShowBlog';
import BlogSkeleton from '../components/BlogSkeleton';
import { globalHistory } from "@reach/router";
import Metadata from '../components/Metadata';
import {BlogPageMetadata} from '../configs/PageMetadata';
import { BlogGetById } from "../services/BlogService";

const useStyles = makeStyles(() => ({
  msgCard: {
    margin: 20,
    padding: 20
  }
}));

export default function BlogPage() {
  const classes = useStyles();
  const [blogData, setBlogData] = useState(null);
  const [isEmptyData, setEmptyData] = useState(null);

  var blogId = undefined;
  if (globalHistory.location.hash !== undefined) {
    blogId = globalHistory.location.hash.replace("#", '');
    console.log(globalHistory.location);    
  }
  useEffect(() => {
    // debugger;
    if(blogId !== undefined){
      BlogGetById(blogId)
        .then((response) => {
            if (response !== undefined) {
                setBlogData(response);
            }
            else {
                setEmptyData(true);
            }
        })
        .catch(error => {
            setEmptyData(true);
        });
    }
}, []);

  return (
    <Layout>
      
      { (blogId === undefined || blogId === "") ?
        (<Paper elevation={3} className={classes.msgCard} >
          <Typography variant="h5">
            Blog Not Found !
          </Typography>
        </Paper>)
        : (blogData === null) ? 
              (<BlogSkeleton />):(
              <div><ShowBlog blog={blogData} />
              <Metadata Title={blogData.title + " | Blog | Bipul's Web"} Description={"Read Bipul's Blog on : '" + blogData.title + "' | Bipul's Web"} /></div>) 
      }
    </Layout>
  );
}