import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import CardHeader from '@material-ui/core/CardHeader';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import { red } from '@material-ui/core/colors';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import TelegramIcon from '@material-ui/icons/Telegram';
import CommentIcon from '@material-ui/icons/Comment';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import Skeleton from '@material-ui/lab/Skeleton';
import { ShareSiteUrl } from '../configs/ShareUrlConfig';

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 500,
        margin: 20
    },
    msgCard: {
        margin: 20,
        padding: 20
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
}));

export default function LoadBlog(props) {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

    return (
        <Card className={classes.root}>
            <CardHeader
                avatar={
                    <Skeleton animation="wave" variant="circle" width={40} height={40} />
                }
                action={
                    <IconButton aria-label="settings">
                        <MoreVertIcon />
                    </IconButton>
                }

                title={
                    <Skeleton animation="wave" height={10} width="80%" style={{ marginBottom: 6 }} />
                }
                subheader={
                    <Skeleton animation="wave" height={10} width="40%" />
                }
            />
            {
                <Skeleton animation="wave" variant="rect" className={classes.media} />
            }

            <CardContent>
                {
                    <React.Fragment>
                        <Skeleton animation="wave" height={10} style={{ marginBottom: 6 }} />
                        <Skeleton animation="wave" height={10} width="80%" />
                    </React.Fragment>
                }
            </CardContent>
            <CardActions disableSpacing>
                <a href={ShareSiteUrl.TelegramChat} target="_blank">
                    <IconButton aria-label="comment">
                        <CommentIcon />
                    </IconButton>
                </a>
                <a href={ShareSiteUrl.WhatsApp} target="_blank">
                    <IconButton aria-label="comment">
                        <WhatsAppIcon />
                    </IconButton>
                </a>
                <a href={ShareSiteUrl.Telegram} target="_blank">
                    <IconButton aria-label="comment">
                        <TelegramIcon />
                    </IconButton>
                </a>
                <a href={ShareSiteUrl.Facebook} target="_blank">
                    <IconButton aria-label="comment">
                        <FacebookIcon />
                    </IconButton>
                </a>
                <a href={ShareSiteUrl.Twitter} target="_blank">
                    <IconButton aria-label="comment">
                        <TwitterIcon />
                    </IconButton>
                </a>
                <IconButton
                    className={clsx(classes.expand, {
                        [classes.expandOpen]: expanded,
                    })}
                    aria-expanded={expanded}
                    aria-label="show more"
                >
                    <ExpandMoreIcon />
                </IconButton>
            </CardActions>
            <Collapse in={expanded} timeout="auto" unmountOnExit>
                <CardContent>
                    {<div></div>}
                </CardContent>
            </Collapse>
        </Card>
    );
}